<h1 id="商户入驻" tabindex="-1"><a class="header-anchor" href="#商户入驻" aria-hidden="true">#</a> 商户入驻</h1>
<h2 id="申请入驻" tabindex="-1"><a class="header-anchor" href="#申请入驻" aria-hidden="true">#</a> 申请入驻</h2>
<p>使用申请入驻接口提交你的小微商户资料。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token function">submitApplication</span><span class="token punctuation">(</span><span class="token punctuation">[</span>
    <span class="token string single-quoted-string">'business_code'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'123456'</span><span class="token punctuation">,</span> <span class="token comment">// 业务申请编号</span>
    <span class="token string single-quoted-string">'id_card_copy'</span>  <span class="token operator">=></span> <span class="token string single-quoted-string">'media_id'</span><span class="token punctuation">,</span> <span class="token comment">// 身份证人像面照片</span>
    <span class="token comment">// ...</span>
    <span class="token comment">// 参数太多就不一一列出，自行根据 (小微商户专属文档 -> 申请入驻api) 填写</span>
<span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br></div></div><h2 id="查询申请状态" tabindex="-1"><a class="header-anchor" href="#查询申请状态" aria-hidden="true">#</a> 查询申请状态</h2>
<p>使用申请入驻接口提交小微商户资料后，一般5分钟左右可以通过该查询接口查询具体的申请结果。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$applymentId</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'商户申请单号(applyment_id 申请入驻接口返回)'</span><span class="token punctuation">;</span>
<span class="token variable">$businessCode</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'业务申请编号(business_code)'</span><span class="token punctuation">;</span>
<span class="token variable">$app</span><span class="token operator">-></span><span class="token function">getStatus</span><span class="token punctuation">(</span><span class="token keyword type-hint">string</span> <span class="token variable">$applymentId</span><span class="token punctuation">,</span> <span class="token keyword type-hint">string</span> <span class="token variable">$businessCode</span> <span class="token operator">=</span> <span class="token string single-quoted-string">''</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><blockquote>
<p>商户申请单号和业务申请编号填写一个就行了，当 <code>applyment_id</code> 已填写时，<code>business_code</code> 字段无效。</p>
</blockquote>
<p>当查询申请状态为待签约，接口会一并返回签约二维码，服务商需引导商户使用本人微信扫码完成协议签署。</p>
